"use client";
import { useSession, signIn, signOut } from "next-auth/react";
import React, { useState, FormEvent, ChangeEvent } from 'react';
import { useRouter } from "next/navigation";
import Link from "next/link";
import { ShieldCheck, LogIn, AlertCircle } from 'lucide-react'; // Removed Chrome icon

const Login: React.FC = () => {
  const router = useRouter();
  const { data: session, status } = useSession();

  const [formData, setFormData] = useState({ email: '', password: '' });
  const [error, setError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  // ... (handleChange and handleSubmit remain exactly the same)

  if (status === "loading") {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen bg-[#121212]">
        <span className="loading loading-spinner loading-lg text-amber-500"></span>
        <p className="mt-4 text-gray-400 animate-pulse font-mono">Verifying credentials...</p>
      </div>
    );
  }

  if (session) {
    // ... (Keep the "Access Granted" view as is)
  }

  function handleChange(event: ChangeEvent<HTMLInputElement>): void {
    throw new Error("Function not implemented.");
  }

  return (
    <div className="flex min-h-screen flex-col justify-center bg-[#121212] px-6 py-12 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-sm text-center">
        <div className="flex justify-center mb-6">
          <LogIn className="text-amber-500" size={40} />
        </div>
        <h2 className="text-3xl font-bold tracking-tight text-white font-serif">
          Management Portal
        </h2>
        <p className="mt-2 text-gray-500 text-sm">
          Secure Administrative Access
        </p>
      </div>

      <div className="mt-10 sm:mx-auto sm:w-full sm:max-w-sm">
        <form onSubmit={handleSubmit} className="space-y-6">
          {error && (
            <div className="flex items-center gap-2 p-3 text-sm text-red-400 bg-red-400/10 border border-red-400/20 rounded-md">
              <AlertCircle size={16} />
              {error}
            </div>
          )}

          <div>
            <label className="block text-sm font-medium text-gray-300">Identity (Email)</label>
            <input
              name="email"
              type="email"
              required
              value={formData.email}
              onChange={handleChange}
              className="mt-2 block w-full rounded-md border-0 bg-white/5 py-2.5 px-3 text-white ring-1 ring-white/10 focus:ring-2 focus:ring-amber-500 sm:text-sm"
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-300">Access Key</label>
            <input
              name="password"
              type="password"
              required
              value={formData.password}
              onChange={handleChange}
              className="mt-2 block w-full rounded-md border-0 bg-white/5 py-2.5 px-3 text-white ring-1 ring-white/10 focus:ring-2 focus:ring-amber-500 sm:text-sm"
            />
          </div>

          <button
            type="submit"
            disabled={isSubmitting}
            className={`flex w-full justify-center rounded-md bg-amber-600 px-3 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-amber-500 transition-all ${isSubmitting ? 'opacity-50' : ''}`}
          >
            {isSubmitting ? 'Authenticating...' : 'Secure Login'}
          </button>

          {/* THE GOOGLE BUTTON SECTION HAS BEEN REMOVED */}
        </form>
      </div>
    </div>
  );
};

export default Login;